import Vue from 'vue'
import Router from 'vue-router'
const HelloWorld= () =>  import('@/components/HelloWorld')
const uploadplot= () =>  import('@/components/uploadplot')
const login= () =>  import('@/components/login')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/uploadplot/:id',
      name: 'uploadplot',
      component: uploadplot
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path:'*',
      redirect:'/HelloWorld'
    }
  ]
})



